/* Global Styles */
:root {
  --primary-bg: #0c0c14;
  --secondary-bg: #16161e;
  --primary-text: #ffffff;
  --secondary-text: #a9b3c9;
  --accent-color: #C00000;
  --accent-secondary: #850000;
  --accent-danger: #ff3860;
  --accent-gradient: linear-gradient(135deg, #C00000, #850000);
  --card-bg: rgba(15, 15, 25, 0.7);
  --glass-effect: rgba(20, 20, 35, 0.3);
  --box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.36);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background: var(--primary-bg);
  color: var(--primary-text);
  line-height: 1.6;
  background-image: 
    radial-gradient(circle at 15% 50%, rgba(192, 0, 0, 0.08) 0%, transparent 25%),
    radial-gradient(circle at 85% 30%, rgba(192, 0, 0, 0.05) 0%, transparent 33%);
  background-attachment: fixed;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

section {
  padding: 4.5rem 0;
  scroll-margin-top: 80px;
  position: relative;
}

h1, h2, h3, h4, h5, h6 {
  margin-bottom: 1rem;
  font-weight: 700;
  letter-spacing: -0.5px;
}

h2 {
  font-size: 2.75rem;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 3rem;
  background: var(--accent-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 800;
}

p {
  margin-bottom: 1rem;
  color: var(--secondary-text);
  font-size: 1.05rem;
}

button, .button {
  padding: 0.9rem 1.8rem;
  background: var(--accent-gradient);
  color: var(--primary-text);
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  text-transform: uppercase;
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
  box-shadow: 0 6px 15px rgba(192, 0, 0, 0.3);
  letter-spacing: 1px;
}

button:before, .button:before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: all 0.6s ease;
}

button:hover, .button:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(192, 0, 0, 0.4);
}

button:hover:before, .button:hover:before {
  left: 100%;
}

.section-title {
  position: relative;
  display: inline-block;
  margin-bottom: 4rem;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -12px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: var(--accent-gradient);
  border-radius: 3px;
}

/* Glass Effect Cards */
.glass-card {
  background: var(--glass-effect);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: var(--box-shadow);
}

/* Animation Classes */
.fade-in {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.8s ease, transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Parallax effect */
.parallax {
  transition: transform 0.5s cubic-bezier(0.215, 0.610, 0.355, 1);
}

/* Responsive Design */
@media (max-width: 768px) {
  h2 {
    font-size: 2.2rem;
  }
  
  section {
    padding: 5rem 0;
  }
}

/* Enhanced Button Hover Effects */
button:active, .button:active {
  transform: scale(0.97);
}

/* Modern Selection Styling */
::selection {
  background-color: rgba(192, 0, 0, 0.7);
  color: white;
}

/* Text Link Hover Effect */
a:not(.button):not(.nav-links a):not(.social-links a) {
  position: relative;
  text-decoration: none;
}

a:not(.button):not(.nav-links a):not(.social-links a)::after {
  content: '';
  position: absolute;
  width: 0;
  height: 1px;
  bottom: -2px;
  left: 0;
  background-color: var(--accent-color);
  transition: width 0.3s ease;
}

a:not(.button):not(.nav-links a):not(.social-links a):hover::after {
  width: 100%;
}

/* Image Hover Effects */
img:not(.logo-card img) {
  transition: all 0.4s ease;
}

img:not(.logo-card img):hover {
  filter: drop-shadow(0 0 20px rgba(192, 0, 0, 0.3));
}

/* Loading Animation */
.loading {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.loading div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: var(--accent-color);
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.loading div:nth-child(1) {
  left: 8px;
  animation: loading1 0.6s infinite;
}

.loading div:nth-child(2) {
  left: 8px;
  animation: loading2 0.6s infinite;
}

.loading div:nth-child(3) {
  left: 32px;
  animation: loading2 0.6s infinite;
}

.loading div:nth-child(4) {
  left: 56px;
  animation: loading3 0.6s infinite;
}

@keyframes loading1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes loading3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes loading2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}

/* Enhance all glass effects */
.glass-card {
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 
    0 8px 32px 0 rgba(0, 0, 0, 0.36),
    0 0 0 1px rgba(255, 255, 255, 0.05) inset;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.glass-card:hover {
  box-shadow: 
    0 15px 40px 0 rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 255, 255, 0.07) inset;
}

/* Modern Scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: var(--primary-bg);
}

::-webkit-scrollbar-thumb {
  background: #333;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-color);
} 