#clients {
  position: relative;
  padding: 8rem 0;
  background-color: var(--primary-bg);
  overflow: hidden;
  z-index: 1;
}

.clients-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(10, 10, 15, 0.95), rgba(20, 20, 30, 0.97));
  overflow: hidden;
  z-index: 0;
}

/* Code elements styling */
.code-elements {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.floating-icon {
  position: absolute;
  color: rgba(192, 0, 0, 0.3);
  filter: drop-shadow(0 0 10px rgba(192, 0, 0, 0.2));
  z-index: 1;
}

.code-snippet {
  position: absolute;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.2);
  z-index: 1;
  white-space: nowrap;
}

.clients-header {
  text-align: center;
  max-width: 700px;
  margin: 0 auto 4rem;
  position: relative;
  z-index: 2;
}

.section-title {
  font-size: 3rem;
  margin-bottom: 1rem;
  background: linear-gradient(90deg, #c00000, #ff3333);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  position: relative;
  z-index: 2;
  text-align: center;
}

.clients-subtitle {
  font-size: 1.2rem;
  color: var(--secondary-text);
  margin-bottom: 1rem;
  position: relative;
  z-index: 2;
  text-align: center;
}

.logo-slider {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
  padding: 3rem 2rem;
  background: rgba(15, 15, 20, 0.2);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.3),
    0 0 80px rgba(192, 0, 0, 0.1),
    inset 0 0 20px rgba(192, 0, 0, 0.05);
  z-index: 2;
  overflow: hidden;
}

/* Create the glowing edge effect */
.logo-slider::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(45deg, transparent, rgba(192, 0, 0, 0.2), transparent);
  border-radius: 22px;
  z-index: -1;
  pointer-events: none;
}

.logo-container.infinite-scroll {
  position: relative;
  overflow: hidden;
  height: 160px;
  margin: 0;
  z-index: 2;
  width: 100%;
}

.logo-track {
  display: flex;
  height: 100%;
  width: fit-content;
}

.logo-slide {
  height: 100%;
  min-width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1.5rem;
}

.logo-card {
  position: relative;
  width: 100%;
  height: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(25, 25, 35, 0.5);
  border-radius: 15px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  transition: all 0.4s ease;
}

.logo-card:hover {
  background: rgba(35, 35, 45, 0.6);
  border-color: rgba(192, 0, 0, 0.2);
  box-shadow: 
    0 15px 30px rgba(0, 0, 0, 0.25),
    0 0 15px rgba(192, 0, 0, 0.15);
}

.logo-glow {
  position: absolute;
  width: 100px;
  height: 100px;
  background: radial-gradient(circle, rgba(192, 0, 0, 0.15), transparent 70%);
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.5s ease;
  z-index: 1;
  pointer-events: none;
}

.logo-card:hover .logo-glow {
  opacity: 1;
}

.client-logo {
  position: relative;
  max-width: 70%;
  max-height: 70px;
  opacity: 0.9;
  transition: all 0.5s ease;
  filter: brightness(1.1) drop-shadow(0 5px 10px rgba(0, 0, 0, 0.1));
  object-fit: contain;
  z-index: 2;
}

.logo-card:hover .client-logo {
  opacity: 1;
  filter: brightness(1.3) drop-shadow(0 5px 15px rgba(0, 0, 0, 0.2));
  transform: scale(1.08);
}

.client-logo.google {
  max-height: 50px;
}

.client-logo.amazon {
  max-height: 55px;
}

.client-logo.netflix {
  max-height: 55px;
}

.client-logo.github {
  max-height: 60px;
}

/* Remove the slider controls and dots */
.slider-controls {
  display: none;
}

.slider-dots {
  display: none;
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .logo-slide {
    min-width: 200px;
  }
}

@media (max-width: 767px) {
  .logo-slider {
    padding: 2rem 1rem;
  }
  
  .logo-container.infinite-scroll {
    height: 120px;
  }
  
  .logo-slide {
    min-width: 180px;
    padding: 0 1rem;
  }
  
  .logo-card {
    height: 100px;
  }
  
  .client-logo {
    max-height: 50px;
  }
} 