footer {
  background-color: rgba(10, 10, 15, 0.95);
  color: var(--secondary-text);
  padding: 4rem 0 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  position: relative;
  overflow: hidden;
}

footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    90deg, 
    transparent 0%, 
    rgba(192, 0, 0, 0.2) 20%, 
    rgba(255, 0, 0, 0.8) 50%,
    rgba(192, 0, 0, 0.2) 80%, 
    transparent 100%
  );
  box-shadow: 
    0 0 10px rgba(255, 0, 0, 0.5),
    0 0 20px rgba(255, 0, 0, 0.3);
  z-index: 5;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 3rem;
}

.footer-brand {
  flex: 1;
  max-width: 400px;
}

/* New styles for logo and text alignment */
.footer-logo-container {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
}

.footer-logo {
  width: 100px;
  height: 100px;
  object-fit: contain;
  margin-right: 1rem;
  filter: drop-shadow(0 0 5px rgba(192, 0, 0, 0.3));
}

.footer-brand-text {
  display: flex;
  flex-direction: column;
}

.footer-brand-text h3 {
  font-size: 1.8rem;
  margin: 0;
  background: linear-gradient(90deg, #c00000, #ff3333);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.footer-brand-text p {
  font-size: 0.9rem;
  margin: 0;
  color: #ffffff;
}

.footer-description {
  font-size: 1rem;
  line-height: 1.6;
  color: var(--secondary-text);
  margin-bottom: 1.5rem;
}

.footer-links {
  display: flex;
  gap: 4rem;
}

.footer-links-column h4,
.footer-contact h4 {
  color: var(--primary-text);
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  position: relative;
}

.footer-links-column h4::after,
.footer-contact h4::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 30px;
  height: 2px;
  background: var(--accent-color);
}

.footer-links-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links-column ul li {
  margin-bottom: 0.8rem;
}

.footer-links-column ul li a {
  color: var(--secondary-text);
  text-decoration: none;
  transition: all 0.3s ease;
  position: relative;
  padding-left: 0;
}

.footer-links-column ul li a:hover {
  color: var(--accent-color);
  padding-left: 5px;
}

.footer-links-column ul li a::before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 1px;
  background: var(--accent-color);
  transition: all 0.3s ease;
}

.footer-links-column ul li a:hover::before {
  width: 100%;
}

.footer-contact p {
  margin-bottom: 0.8rem;
  font-size: 1rem;
}

.social-icons {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

.social-icons a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.05);
  color: var(--secondary-text);
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.social-icons a:hover {
  background: var(--accent-color);
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(192, 0, 0, 0.3);
}

.footer-bottom {
  text-align: center;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  font-size: 0.9rem;
}

@media (max-width: 992px) {
  .footer-content {
    flex-direction: column;
    gap: 3rem;
  }
  
  .footer-brand {
    max-width: 100%;
  }
  
  .footer-links {
    width: 100%;
    justify-content: space-between;
  }
}

@media (max-width: 768px) {
  footer {
    padding: 3rem 0 2rem;
  }
  
  .footer-links {
    flex-direction: column;
    gap: 2rem;
  }
  
  .footer-logo {
    width: 50px;
    height: 50px;
  }
  
  .footer-brand-text h3 {
    font-size: 1.5rem;
  }
} 