header {
  height: 100vh;
  min-height: 700px;
  display: flex;
  align-items: center;
  background-color: var(--primary-bg);
  overflow: hidden;
  position: relative;
}

.cyber-grid {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    linear-gradient(to right, rgba(192, 0, 0, 0.03) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(192, 0, 0, 0.03) 1px, transparent 1px);
  background-size: 30px 30px;
  z-index: 0;
  transform: perspective(1000px) rotateX(60deg) scale(2.5);
  transform-origin: center bottom;
  opacity: 0.2;
}

/* Particles Animation */
.particles-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 1;
}

.particle {
  position: absolute;
  width: 2px;
  height: 2px;
  background-color: var(--accent-color);
  border-radius: 50%;
  opacity: 0.4;
  animation: float-particle linear infinite;
}

@keyframes float-particle {
  0% {
    transform: translateY(0) translateX(0);
  }
  25% {
    transform: translateY(-30px) translateX(15px);
  }
  50% {
    transform: translateY(-15px) translateX(30px);
  }
  75% {
    transform: translateY(20px) translateX(15px);
  }
  100% {
    transform: translateY(0) translateX(0);
  }
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 2;
}

.header-content {
  flex: 1;
  z-index: 2;
  max-width: 600px;
}

.company-badge {
  display: inline-flex;
  align-items: center;
  background: rgba(192, 0, 0, 0.1);
  padding: 0.5rem 1rem;
  border-radius: 50px;
  margin-bottom: 1.5rem;
  border: 1px solid rgba(192, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.badge-icon {
  color: var(--accent-color);
  margin-right: 0.5rem;
}

.company-badge span {
  font-size: 0.8rem;
  font-weight: 600;
  letter-spacing: 1px;
}

/* Glitch effect for h1 */
h1.glitch {
  font-size: 4.5rem;
  font-weight: 900;
  letter-spacing: 2px;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  color: var(--primary-text);
  position: relative;
  text-shadow: 0 0 10px rgba(192, 0, 0, 0.3);
}

h1.glitch::before,
h1.glitch::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--primary-bg);
}

h1.glitch::before {
  color: #ff0000;
  z-index: -2;
  clip-path: polygon(0 0, 100% 0, 100% 45%, 0 45%);
  transform: translate(-5px, 5px);
  animation: glitch-anim-1 3s infinite linear alternate-reverse;
}

h1.glitch::after {
  color: #0000ff;
  z-index: -1;
  clip-path: polygon(0 80%, 100% 20%, 100% 100%, 0 100%);
  transform: translate(5px, -5px);
  animation: glitch-anim-2 2.7s infinite linear alternate-reverse;
}

@keyframes glitch-anim-1 {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-3px, 3px);
  }
  40% {
    transform: translate(-3px, -3px);
  }
  60% {
    transform: translate(3px, 3px);
  }
  80% {
    transform: translate(3px, -3px);
  }
  100% {
    transform: translate(0);
  }
}

@keyframes glitch-anim-2 {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(5px, 5px);
  }
  40% {
    transform: translate(5px, -5px);
  }
  60% {
    transform: translate(-5px, 5px);
  }
  80% {
    transform: translate(-5px, -5px);
  }
  100% {
    transform: translate(0);
  }
}

.tagline-wrapper {
  position: relative;
  display: inline-block;
  margin-bottom: 1.5rem;
}

.tagline {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  color: var(--primary-text);
  letter-spacing: -0.5px;
  background: linear-gradient(to right, #ffffff, #a0a0a0);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.tagline-accent {
  position: absolute;
  bottom: -5px;
  left: 0;
  height: 3px;
  width: 120px;
  background: var(--accent-gradient);
  border-radius: 3px;
}

.sub-tagline {
  font-size: 1.1rem;
  margin-bottom: 2.5rem;
  max-width: 500px;
  line-height: 1.7;
  color: rgba(255, 255, 255, 0.8);
}

.cta-container {
  display: flex;
  gap: 1rem;
  margin-bottom: 2.5rem;
}

.cta-button {
  font-size: 1rem;
  padding: 1rem 2rem;
  background: var(--accent-gradient);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  box-shadow: 0 4px 15px rgba(192, 0, 0, 0.3);
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
  overflow: hidden;
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(192, 0, 0, 0.4);
}

.btn-icon {
  font-size: 1.1rem;
  transition: transform 0.3s ease;
}

.cta-button:hover .btn-icon {
  transform: scale(1.2);
}

.secondary-button {
  font-size: 1rem;
  padding: 1rem 2rem;
  background: transparent;
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 10px;
}

.secondary-button:hover {
  border-color: var(--accent-color);
  background: rgba(192, 0, 0, 0.1);
  transform: translateY(-3px);
}

.btn-arrow {
  transition: transform 0.3s ease;
}

.secondary-button:hover .btn-arrow {
  transform: translateX(5px);
}

.stats-container {
  display: flex;
  gap: 2rem;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 10px;
  background: rgba(15, 15, 25, 0.4);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
}

.stat-item:hover {
  transform: translateY(-5px);
  background: rgba(15, 15, 25, 0.6);
}

.stat-icon {
  font-size: 1.8rem;
  color: var(--accent-color);
}

.stat-content {
  display: flex;
  flex-direction: column;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-text);
}

.stat-label {
  font-size: 0.9rem;
  color: var(--secondary-text);
}

/* Header Image Container */
.header-image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  min-height: 500px;
}

.image-frame {
  position: relative;
  width: 100%;
  max-width: 450px;
  z-index: 2;
}

.raven-image {
  max-width: 100%;
  height: auto;
  filter: drop-shadow(0 0 20px rgba(192, 0, 0, 0.4));
  animation: float 6s ease-in-out infinite;
  border-radius: 10px;
  position: relative;
  z-index: 3;
}

.image-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(192, 0, 0, 0.2) 0%, transparent 70%);
  filter: blur(40px);
  z-index: 1;
}

/* Tech decorations */
.tech-decoration {
  position: absolute;
  border-radius: 12px;
  background: transparent !important;
  backdrop-filter: none !important;
  -webkit-backdrop-filter: none !important;
  border: none !important;
  padding: 1.2rem;
  z-index: 5;
  box-shadow: none !important;
  transition: all 0.3s ease;
  overflow: visible !important;
  min-width: 280px;
}

.tech-decoration.top {
  top: 25% !important;
  right: -15px;
  width: auto !important;
  max-width: none !important;
  transform: rotate(2deg);
}

.tech-decoration.bottom {
  bottom: 20%;
  left: 5%;
}

/* Code snippet */
.code-snippet {
  font-family: 'Courier New', monospace;
  font-size: 0.95rem;
  color: #ffffff;
  position: relative;
  width: auto !important;
  white-space: nowrap;
  margin-top: 0;
}

.code-snippet::before {
  content: none;
}

.code-snippet::after {
  content: none;
}

/* Target code lines with more specific selectors */
.tech-decoration .code-snippet .code-line {
  margin-bottom: 0.8rem;
  opacity: 1 !important;
  transform: translateY(0) !important;
  line-height: 1.5;
  white-space: nowrap !important;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.8) !important;
  display: block !important;
  visibility: visible !important;
  width: auto !important;
  padding-left: 5px;
  background: transparent !important;
}

/* Override the nth-child(odd) style that's changing colors */
.tech-decoration .code-snippet .code-line:nth-child(odd) {
  color: inherit !important; /* This removes the #c0c0c0 color */
}

/* Ensure each line has its specific color */
.tech-decoration .code-snippet .code-line:nth-child(1) {
  color: #ff9f9f !important; /* Red for import statement */
}

.tech-decoration .code-snippet .code-line:nth-child(2) {
  color: #b8d8ff !important; /* Blue for variable declaration */
}

.tech-decoration .code-snippet .code-line:nth-child(3) {
  color: #c2ffb8 !important; /* Green for function calls */
}

.tech-decoration .code-snippet .code-line:nth-child(4) {
  color: #c2ffb8 !important; /* Green for function calls */
}

/* Tech pills */
.tech-pills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-pill {
  padding: 0.3rem 0.8rem;
  background: rgba(192, 0, 0, 0.2);
  border-radius: 50px;
  font-size: 0.8rem;
  font-weight: 600;
  color: #fff;
  border: 1px solid rgba(192, 0, 0, 0.3);
}

/* Floating icons */
.floating-icon {
  position: absolute;
  font-size: 1.5rem;
  color: var(--accent-color);
  background: rgba(15, 15, 25, 0.6);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  z-index: 3;
}

.icon-1 {
  top: 30%;
  left: 10%;
  animation: float 7s ease-in-out infinite;
}

.icon-2 {
  top: 70%;
  left: 15%;
  animation: float 8s ease-in-out infinite;
  animation-delay: 1s;
}

.tech-circle {
  position: absolute;
  width: 250px;
  height: 250px;
  border: 1px dashed rgba(192, 0, 0, 0.3);
  border-radius: 50%;
  animation: rotate 20s linear infinite;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.tech-circle.small {
  width: 150px;
  height: 150px;
  border: 1px dashed rgba(255, 255, 255, 0.1);
  animation-direction: reverse;
  animation-duration: 15s;
}

.tech-circle::before {
  content: '';
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: 10px;
  height: 10px;
  background-color: var(--accent-color);
  border-radius: 50%;
  box-shadow: 0 0 10px var(--accent-color);
}

.tech-circle.small::before {
  background-color: #fff;
  box-shadow: 0 0 10px #fff;
}

/* Scroll indicator */
.scroll-indicator {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 10;
}

.chevron {
  margin: 0 auto;
  width: 30px;
  height: 8px;
  opacity: 0;
  transform: scale(0.3);
  animation: move-chevron 3s ease-out infinite;
}

.chevron:first-child {
  animation-delay: 0s;
}

.chevron:nth-child(2) {
  animation-delay: 0.5s;
}

.chevron:nth-child(3) {
  animation-delay: 1s;
}

.chevron:before,
.chevron:after {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: 50%;
  background: var(--accent-color);
}

.chevron:before {
  left: 0;
  transform: skewY(30deg);
}

.chevron:after {
  right: 0;
  transform: skewY(-30deg);
}

.scroll-text {
  color: var(--secondary-text);
  font-size: 0.9rem;
  margin-top: 10px;
  opacity: 0.7;
}

@keyframes float {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(2deg);
  }
  100% {
    transform: translateY(0px) rotate(0deg);
  }
}

@keyframes rotate {
  from {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes move-chevron {
  25% {
    opacity: 1;
    transform: translateY(10px) scale(0.8);
  }
  33% {
    opacity: 1;
    transform: translateY(20px) scale(0.9);
  }
  67% {
    opacity: 1;
    transform: translateY(30px) scale(0.9);
  }
  100% {
    opacity: 0;
    transform: translateY(40px) scale(0.9);
  }
}

/* Responsive styles */
@media (max-width: 1200px) {
  .header-container {
    flex-direction: column;
    text-align: center;
    padding-top: 120px;
    gap: 3rem;
  }
  
  .header-content {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .tagline-accent {
    left: 50%;
    transform: translateX(-50%);
  }
  
  .stats-container {
    justify-content: center;
  }
  
  .tech-decoration.top {
    position: absolute !important;
    top: 15% !important;
    right: 0 !important;
    display: block !important;
    visibility: visible !important;
    opacity: 1 !important;
    max-width: none !important;
  }
  
  .tech-decoration.bottom {
    bottom: 10%;
    left: 0;
  }
  
  .floating-icon.icon-1 {
    left: 5%;
  }
  
  .floating-icon.icon-2 {
    top: 60%;
    left: 10%;
  }
}

@media (max-width: 768px) {
  header {
    height: auto;
    min-height: 100vh;
    padding: 120px 0 60px;
  }
  
  h1.glitch {
    font-size: 3.2rem;
  }
  
  .tagline {
    font-size: 1.5rem;
  }
  
  .cta-container {
    flex-direction: column;
    width: 100%;
    max-width: 350px;
  }
  
  .stats-container {
    flex-direction: column;
    width: 100%;
    max-width: 350px;
  }
  
  .tech-decoration {
    display: block !important;
    position: absolute !important;
    opacity: 1 !important;
    visibility: visible !important;
    top: auto !important;
    bottom: 15% !important;
    right: 5% !important;
    max-width: none !important;
    transform: rotate(2deg) !important;
  }
  
  .floating-icon {
    display: none;
  }
  
  .image-frame {
    max-width: 300px;
  }
} 