#contact {
  position: relative;
  padding: 8rem 0 10rem;
  overflow: hidden;
  background-color: var(--primary-bg);
  z-index: 1;
}

.contact-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.bg-elements {
  width: 100%;
  height: 100%;
  position: relative;
}

.floating-particle {
  position: absolute;
  width: 5px;
  height: 5px;
  background: rgba(192, 0, 0, 0.3);
  border-radius: 50%;
  filter: blur(1px);
  box-shadow: 0 0 10px rgba(192, 0, 0, 0.5);
  z-index: 1;
}

.grid-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    linear-gradient(90deg, rgba(192, 0, 0, 0.03) 1px, transparent 1px) 0 0 / 30px 30px,
    linear-gradient(rgba(192, 0, 0, 0.03) 1px, transparent 1px) 0 0 / 30px 30px;
}

.contact-header {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 4rem;
}

.section-title {
  font-size: 3rem;
  margin-bottom: 1rem;
  background: linear-gradient(90deg, #c00000, #ff3333);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  position: relative;
  z-index: 2;
}

.contact-subtitle {
  font-size: 1.2rem;
  color: var(--secondary-text);
  margin-bottom: 2rem;
}

.contact-content {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  gap: 3rem;
  position: relative;
  z-index: 2;
}

.contact-info {
  flex: 1;
  position: relative;
}

.info-container {
  background: rgba(15, 15, 20, 0.5);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  overflow: hidden;
  padding: 2.5rem;
  height: 100%;
  box-shadow: 
    0 10px 25px rgba(0, 0, 0, 0.4),
    0 0 40px rgba(192, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
}

.info-container h3 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  background: linear-gradient(90deg, #ffffff, rgba(255, 255, 255, 0.8));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.info-text {
  color: var(--secondary-text);
  margin-bottom: 2.5rem;
  line-height: 1.6;
}

.contact-methods {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 2.5rem;
}

.contact-method {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.icon-container {
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(192, 0, 0, 0.1);
  border-radius: 50%;
  color: var(--accent-color);
  font-size: 1.2rem;
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.contact-method:hover .icon-container {
  background: var(--accent-color);
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 5px 15px rgba(192, 0, 0, 0.3);
}

.method-details h4 {
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
  color: var(--primary-text);
}

.method-details p {
  color: var(--secondary-text);
  font-size: 0.95rem;
}

.contact-form-container {
  flex: 1.2;
  position: relative;
}

.contact-form {
  background: rgba(20, 20, 30, 0.4);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  overflow: hidden;
  box-shadow: 
    0 10px 25px rgba(0, 0, 0, 0.4),
    0 0 40px rgba(192, 0, 0, 0.15);
}

.form-header {
  display: flex;
  align-items: center;
  padding: 1rem 1.5rem;
  background: rgba(30, 30, 40, 0.6);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.form-dots {
  display: flex;
  gap: 6px;
  margin-right: 1rem;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.dot.red {
  background: #ff5f57;
}

.dot.yellow {
  background: #febc2e;
}

.dot.green {
  background: #28c840;
}

.form-title {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
}

.form-row {
  display: flex;
  gap: 1rem;
}

.form-group {
  margin-bottom: 1.5rem;
  width: 100%;
}

.contact-form {
  padding: 0 0 2rem;
}

.contact-form .form-group {
  padding: 0 2rem;
  margin-top: 1.5rem;
}

label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--secondary-text);
  font-size: 0.95rem;
}

input,
textarea {
  width: 100%;
  padding: 0.8rem 1rem;
  font-size: 1rem;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(30, 30, 40, 0.3);
  color: var(--primary-text);
  transition: all 0.3s ease;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 3px rgba(192, 0, 0, 0.15), 0 0 15px rgba(192, 0, 0, 0.1);
}

input::placeholder,
textarea::placeholder {
  color: rgba(255, 255, 255, 0.3);
}

.form-submit {
  display: flex;
  justify-content: flex-end;
  padding: 0 2rem;
}

.submit-button {
  padding: 0.9rem 2rem;
  background: linear-gradient(to right, #c00000, #ff3333);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  min-width: 180px;
}

.submit-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(192, 0, 0, 0.2);
}

.submit-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.loading-spinner {
  width: 24px;
  height: 24px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.success-message {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  background: rgba(20, 20, 30, 0.4);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 
    0 10px 25px rgba(0, 0, 0, 0.3),
    0 0 40px rgba(192, 0, 0, 0.1);
}

.success-message svg {
  font-size: 4rem;
  color: #28c840;
  margin-bottom: 1.5rem;
}

.success-message h3 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: var(--primary-text);
}

.success-message p {
  color: var(--secondary-text);
  font-size: 1.1rem;
  max-width: 400px;
}

@media (max-width: 1024px) {
  .contact-content {
    flex-direction: column;
  }
  
  .contact-info,
  .contact-form-container {
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  #contact {
    padding: 6rem 0 8rem;
  }
  
  .section-title {
    font-size: 2.5rem;
  }
  
  .contact-subtitle {
    font-size: 1.1rem;
  }
  
  .form-row {
    flex-direction: column;
    gap: 0;
  }
  
  .contact-methods {
    gap: 1rem;
  }
  
  .icon-container {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }
  
  .method-details h4 {
    font-size: 1rem;
  }
  
  .info-container,
  .contact-form {
    padding: 1.5rem;
  }
  
  .contact-form .form-group {
    padding: 0 1rem;
  }
  
  .form-submit {
    padding: 0 1rem;
  }
  
  .light-effect.light-1,
  .light-effect.light-2,
  .light-effect.light-3 {
    width: 60vw;
    height: 60vw;
  }
  
  .floating-logo {
    width: 50px;
    height: 50px;
  }
  
  #contact::after {
    height: 1px;
  }
}

/* Light effects */
.light-effect {
  position: absolute;
  border-radius: 50%;
  filter: blur(120px);
  opacity: 0.15;
  z-index: 0;
}

.light-effect.light-1 {
  top: 15%;
  left: 10%;
  width: 40vw;
  height: 40vw;
  background: radial-gradient(circle, rgba(192, 0, 0, 0.4), transparent 70%);
  animation: pulse-light 15s infinite alternate;
}

.light-effect.light-2 {
  bottom: 10%;
  right: 5%;
  width: 35vw;
  height: 35vw;
  background: radial-gradient(circle, rgba(60, 60, 200, 0.3), transparent 70%);
  animation: pulse-light 18s infinite alternate-reverse;
}

.light-effect.light-3 {
  top: 50%;
  left: 50%;
  width: 25vw;
  height: 25vw;
  background: radial-gradient(circle, rgba(192, 0, 0, 0.2), transparent 70%);
  transform: translate(-50%, -50%);
  animation: pulse-light 12s infinite alternate;
}

@keyframes pulse-light {
  0% {
    opacity: 0.1;
    transform: scale(0.8) translate(0, 0);
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 0.15;
    transform: scale(1.2) translate(50px, 20px);
  }
}

/* Floating logos */
.floating-logo {
  position: absolute;
  z-index: 1;
  pointer-events: none;
  width: 70px;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.floating-logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: grayscale(70%) brightness(0.7) opacity(0.2);
  transition: all 0.5s ease;
}

/* Red light line at the bottom of the section */
#contact::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    90deg, 
    transparent 0%, 
    rgba(192, 0, 0, 0.2) 20%, 
    rgba(255, 0, 0, 0.8) 50%,
    rgba(192, 0, 0, 0.2) 80%, 
    transparent 100%
  );
  box-shadow: 
    0 0 10px rgba(255, 0, 0, 0.5),
    0 0 20px rgba(255, 0, 0, 0.3),
    0 0 30px rgba(255, 0, 0, 0.1);
  z-index: 5;
  animation: glow-pulse 3s infinite alternate;
}

/* Animation for the glowing line */
@keyframes glow-pulse {
  0% {
    opacity: 0.7;
    box-shadow: 
      0 0 10px rgba(255, 0, 0, 0.5),
      0 0 20px rgba(255, 0, 0, 0.3);
  }
  100% {
    opacity: 1;
    box-shadow: 
      0 0 15px rgba(255, 0, 0, 0.7),
      0 0 30px rgba(255, 0, 0, 0.5),
      0 0 45px rgba(255, 0, 0, 0.3);
  }
} 