.scroll-progress-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 1002;
}

.scroll-progress-bar {
  height: 100%;
  background: var(--accent-gradient);
  width: 0%;
  transition: width 0.1s ease;
  box-shadow: 0 0 10px rgba(192, 0, 0, 0.5);
} 