#services {
  position: relative;
  padding: 7rem 0;
  background-color: var(--primary-bg);
  overflow: hidden;
}

.services-lighting {
  position: absolute;
  top: -200px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 400px;
  background: radial-gradient(circle, rgba(192, 0, 0, 0.2) 0%, transparent 70%);
  filter: blur(80px);
  z-index: 0;
  opacity: 0.8;
  animation: pulseLight 8s infinite alternate;
}

@keyframes pulseLight {
  0% {
    opacity: 0.6;
    filter: blur(80px);
  }
  100% {
    opacity: 0.9;
    filter: blur(100px);
  }
}

.services-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.services-header {
  text-align: center;
  margin-bottom: 4rem;
}

.services-header h2 {
  font-size: 2.8rem;
  margin-bottom: 1rem;
  background: linear-gradient(to right, #ffffff, rgba(255, 255, 255, 0.7));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.services-header p {
  font-size: 1.2rem;
  color: var(--secondary-text);
  max-width: 700px;
  margin: 0 auto;
}

.service-slider-container {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}

.services-cards {
  display: flex;
  gap: 1.5rem;
  width: 100%;
  overflow: hidden;
  position: relative;
  height: 500px;
}

.service-card {
  flex: 0 0 calc(33.33% - 1rem);
  background: rgba(20, 20, 35, 0.6);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 2.5rem;
  position: relative;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border: 1px solid rgba(255, 255, 255, 0.05);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  transform-style: preserve-3d;
  perspective: 1000px;
}

.service-card.active {
  transform: scale(1.05);
  box-shadow: 0 15px 40px rgba(192, 0, 0, 0.3);
  border-color: rgba(192, 0, 0, 0.3);
}

.service-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(192, 0, 0, 0.1), transparent);
  z-index: -1;
  border-radius: 15px;
}

.service-icon {
  margin-bottom: 1.5rem;
  color: #c00000;
  background: rgba(192, 0, 0, 0.1);
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 20px rgba(192, 0, 0, 0.1);
}

.service-card.active .service-icon {
  animation: pulseIcon 2s infinite alternate;
  box-shadow: 0 0 25px rgba(192, 0, 0, 0.3);
}

@keyframes pulseIcon {
  0% {
    transform: scale(1);
    box-shadow: 0 0 15px rgba(192, 0, 0, 0.2);
  }
  100% {
    transform: scale(1.05);
    box-shadow: 0 0 30px rgba(192, 0, 0, 0.4);
  }
}

.service-card h3 {
  font-size: 1.6rem;
  margin-bottom: 1rem;
  color: var(--primary-text);
  position: relative;
}

.service-card h3::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 40px;
  height: 2px;
  background: linear-gradient(to right, #c00000, transparent);
}

.service-card p {
  color: var(--secondary-text);
  margin-bottom: 1.5rem;
  font-size: 1rem;
  line-height: 1.6;
}

.service-features {
  list-style: none;
  padding: 0;
  margin: 0;
}

.service-features li {
  margin-bottom: 0.8rem;
  color: var(--secondary-text);
  padding-left: 1.5rem;
  position: relative;
}

.service-features li::before {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #c00000;
  box-shadow: 0 0 8px #c00000;
}

.service-hover-effect {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, transparent, rgba(192, 0, 0, 0.05));
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  border-radius: 15px;
}

.service-card:hover .service-hover-effect {
  opacity: 1;
}

.glow-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 0%, rgba(192, 0, 0, 0.15), transparent 70%);
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
}

.service-card.active .glow-overlay {
  opacity: 1;
}

.slider-nav {
  background: rgba(30, 30, 45, 0.7);
  border: 1px solid rgba(192, 0, 0, 0.2);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  font-size: 1.5rem;
  z-index: 2;
  transition: all 0.3s ease;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  position: absolute;
  backdrop-filter: blur(5px);
}

.slider-nav:hover {
  background: rgba(192, 0, 0, 0.8);
  transform: scale(1.1);
  box-shadow: 0 0 20px rgba(192, 0, 0, 0.4);
}

.slider-nav.prev {
  left: -25px;
}

.slider-nav.next {
  right: -25px;
}

.slider-indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.slider-indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.slider-indicator.active {
  background: #c00000;
  transform: scale(1.3);
  box-shadow: 0 0 10px rgba(192, 0, 0, 0.5);
}

.services-cta {
  text-align: center;
  margin-top: 2rem;
}

.primary-button {
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #c00000, #850000);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 10px 20px rgba(192, 0, 0, 0.2);
}

.primary-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 30px rgba(192, 0, 0, 0.3);
}

.floating-code-element {
  position: absolute;
  background: rgba(20, 20, 35, 0.4);
  border-radius: 10px;
  padding: 15px;
  font-family: 'Courier New', monospace;
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.7);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  z-index: 0;
  max-width: 250px;
  overflow: hidden;
  opacity: 0.4;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.floating-code-element pre {
  margin: 0;
  white-space: pre-wrap;
  line-height: 1.3;
}

.code-1 {
  top: 10%;
  left: 2%;
  transform: rotate(-5deg);
  animation: float 15s infinite alternate ease-in-out;
}

.code-2 {
  bottom: 15%;
  right: 2%;
  transform: rotate(3deg);
  animation: float 18s infinite alternate-reverse ease-in-out;
}

@keyframes float {
  0% {
    transform: translate(0, 0) rotate(-5deg);
  }
  50% {
    transform: translate(10px, -15px) rotate(-2deg);
  }
  100% {
    transform: translate(-5px, 10px) rotate(-7deg);
  }
}

.services-bottom-light {
  position: absolute;
  bottom: -150px;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, rgba(192, 0, 0, 0.7), transparent);
  box-shadow: 0 0 20px 5px rgba(192, 0, 0, 0.5);
  z-index: 1;
}

@media (max-width: 1200px) {
  .services-cards {
    height: 550px;
  }

  .service-card {
    flex: 0 0 calc(50% - 1rem);
  }
  
  .floating-code-element {
    display: none;
  }
}

@media (max-width: 992px) {
  .services-cards {
    height: 570px;
  }
}

@media (max-width: 768px) {
  .services-header h2 {
    font-size: 2.2rem;
  }

  .services-cards {
    height: 600px;
  }

  .service-card {
    flex: 0 0 100%;
  }

  .slider-nav.prev {
    left: 10px;
  }

  .slider-nav.next {
    right: 10px;
  }
}
