.back-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(192, 0, 0, 0.9), rgba(255, 51, 51, 0.9));
  border: none;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
  box-shadow: 
    0 5px 15px rgba(0, 0, 0, 0.3),
    0 0 20px rgba(192, 0, 0, 0.3),
    inset 0 1px 1px rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(5px);
  transition: all 0.3s ease;
  overflow: hidden;
}

.button-content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

/* Updated styles for the arrow icon */
.arrow-icon {
  position: absolute !important;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%) !important;
  z-index: 2 !important;
  font-size: 22px !important;
  color: white !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  width: 22px !important;
  height: 22px !important;
  opacity: 1 !important; /* Force visibility */
}

/* Make sure the SVG fills the container */
.arrow-icon svg {
  width: 100% !important;
  height: 100% !important;
  display: block !important;
  fill: currentColor !important; /* Ensure the SVG is filled with the current color */
}

@keyframes flash-arrow {
  0%, 100% {
    opacity: 0.3;
    /* Keep the translate in the transform to maintain centering */
    transform: translate(-50%, -50%) scale(0.9);
  }
  50% {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
    filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.8));
  }
}

/* Fix hover effect */
.back-to-top:hover .arrow-icon {
  animation: none;
  opacity: 1;
  transform: translate(-50%, -50%); /* Keep the centering transform */
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.8));
}

/* Pulse animation ring */
.pulse-ring {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  z-index: 1;
}

.pulse-ring::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.3);
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.5);
  }
  70% {
    box-shadow: 0 0 0 15px rgba(255, 255, 255, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .back-to-top {
    width: 50px;
    height: 50px;
    bottom: 20px;
    right: 20px;
  }
  
  .arrow-icon {
    font-size: 18px;
  }
} 