.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: 1rem 0;
  transition: all 0.3s ease;
  background: rgba(8, 8, 12, 0.2);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

.navbar.scrolled {
  background: rgba(8, 8, 12, 0.85);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  padding: 0.7rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.03);
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: white;
  z-index: 10;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo-img {
  width: 70px;
  height: 70px;
  object-fit: contain;
  filter: drop-shadow(0 0 8px rgba(192, 0, 0, 0.4));
  transition: all 0.3s ease;
}

.logo-text {
  display: flex;
  flex-direction: column;
  margin-left: 5px;
}

.company-name {
  font-size: 1.4rem;
  font-weight: 700;
  letter-spacing: 0.5px;
  background: linear-gradient(90deg, #c00000, #ff3333);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  margin-top: 6px;
  margin-left: -8px;
}

.nav-links {
  display: flex;
  list-style: none;
  align-items: center;
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin: 0 1rem;
  position: relative;
}

.nav-links a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-size: 0.95rem;
  font-weight: 500;
  padding: 0.5rem 0;
  position: relative;
  transition: all 0.3s ease;
}

.nav-links a:hover {
  color: #ffffff;
}

.nav-links a.active {
  color: #ffffff;
  font-weight: 600;
}

.nav-indicator {
  position: absolute;
  bottom: -5px;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #c00000, #ff3333);
  box-shadow: 0 0 8px rgba(192, 0, 0, 0.6);
  border-radius: 4px;
}

.nav-cta {
  margin-left: 1rem !important;
}

.cta-button {
  background: linear-gradient(90deg, #c00000, #ff3333) !important;
  color: white !important;
  padding: 0.7rem 1.5rem !important;
  border-radius: 50px;
  font-weight: 600 !important;
  letter-spacing: 0.5px;
  box-shadow: 0 5px 15px rgba(192, 0, 0, 0.3);
  transition: all 0.3s ease !important;
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(192, 0, 0, 0.4);
}

.menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  cursor: pointer;
  z-index: 10;
}

.menu-toggle span {
  display: block;
  height: 3px;
  width: 100%;
  background: white;
  border-radius: 3px;
  transition: all 0.3s ease;
}

.menu-toggle.active span:nth-child(1) {
  transform: translateY(9px) rotate(45deg);
  background: var(--accent-color);
}

.menu-toggle.active span:nth-child(2) {
  opacity: 0;
}

.menu-toggle.active span:nth-child(3) {
  transform: translateY(-9px) rotate(-45deg);
  background: var(--accent-color);
}

@media (max-width: 992px) {
  .nav-container {
    padding: 0 1.5rem;
  }
  
  .menu-toggle {
    display: flex;
  }
  
  .nav-links {
    position: fixed;
    top: 0;
    right: -100%;
    height: 100vh;
    width: 250px;
    background: rgba(15, 15, 20, 0.98);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    flex-direction: column;
    align-items: flex-start;
    padding: 5rem 2rem;
    transition: all 0.5s ease;
    box-shadow: -5px 0 30px rgba(0, 0, 0, 0.3);
  }
  
  .nav-links.active {
    right: 0;
  }
  
  .nav-links li {
    margin: 1.5rem 0;
    width: 100%;
  }
  
  .nav-indicator {
    left: -10px;
    right: auto;
    height: 100%;
    width: 3px;
    bottom: 0;
    top: 0;
  }
  
  .nav-cta {
    margin-top: 2rem !important;
    width: 100%;
  }
  
  .cta-button {
    display: block;
    text-align: center;
    width: 100%;
  }
}

@media (max-width: 576px) {
  .company-name {
    font-size: 1.2rem;
  }
  
  .logo-img {
    width: 60px;
    height: 60px;
  }
} 